import React from "react";
import {
  Card,
  CardMedia,
  CardContent,
  Typography,
  Grid,
  Box,
} from "@mui/material";

const HomeCards = ({ property }) => {
  return (
    <Box>
      <Card>
        {/* <CardMedia
          component="img"
          height="252"
          image={property.image}
          alt={property.title}
        /> */}
        <Box
          sx={{
            height: { xs: "252px" },
            width: { xs: "100%" },
            backgroundImage: `url(${property.image})`,
            borderRadius: 2,
            p: 1,
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
          }}
        >
          <Box>
            <Chip
              label={property.tag}
              sx={{
                bgcolor: "#3E4C66",
                color: "#fff",
                width: "fit-content",
                borderRadius: 1,
                fontSize: "14px",
              }}
            />
          </Box>
        </Box>
        <CardContent>
          <Typography variant="h6">{property.title}</Typography>
          <Typography variant="body2" color="textSecondary">
            {property.address}
          </Typography>
          <Typography variant="body2">
            Beds: {property.beds} | Baths: {property.baths} | Price: $
            {property.price}
          </Typography>
          {property.type === "For Rent" && (
            <Typography variant="body2" color="textSecondary">
              Available: {property.dateAvailable}
            </Typography>
          )}
        </CardContent>
      </Card>
    </Box>
  );
};

export default HomeCards;
